/* 
this module is used to setup and prepare things on the html side of things, 
add event listners, change some texts, update some icons and stuff like that, 
*/

import Marquee3k from "marquee3000";
export default class HTMLInitializer {
  /* 
  please note that the constructor of this class takes the G variable from the GlobalInit class, 
  this will help us check state of things and update the html accordingly
  */
  constructor(G) {
    this.intializeAudio(G);
    Marquee3k.init();

    this.instructionBg = document.getElementById("instructions");
    // console.log(this.instructionBg.style);
    // this.instructionBg.style.backgroundImage = "url('/img/tshirt.jpg')";
    this.instructionBg.style.background = "transparent";
  }

  intializeAudio(G) {
    this.isMuted = G.IS_MUTE;
    this.muteButton = document.getElementById("mute");

    this.isMuted
      ? (this.muteButton.innerText = "volume_off")
      : (this.muteButton.innerText = "volume_up");

    this.setUpMuteEventListner(G);
  }

  setUpMuteEventListner(G) {
    window.addEventListener("keypress", (e) => {
      if (e.code === "KeyM") {
        if (this.isMuted) {
          this.isMuted = !this.isMuted;
          this.muteButton.innerText = "volume_off";
        } else {
          this.isMuted = !this.isMuted;
          this.muteButton.innerText = "volume_up";
        }

        G.MUTE = this.isMuted;
      }
    });
  }
}
