import { AudioListener, AudioLoader } from "three";

export default class AudioManager {
  constructor(context) {
    this.context = context;

    this.context.listener = new AudioListener();
    this.context.camera.add(this.context.listener);

    this.audioSources = [];

    // console.log("hey there this is the main audio manager waking up");
  }

  addAudioToScene(audio) {
    this.audioSources.push(audio);
    let audioLoader = new AudioLoader();
    audioLoader.load(audio.audioFileUrl, (buffer) => {
      audio.sound.setBuffer(buffer);
      audio.sound.setRefDistance(20);

      audio.parentMesh.add(audio.sound);

      this.playSingleAudio(audio);
    });
  }

  pauseAllAudioInScene() {
    /*   
      iterate over this.audioSources array, check if not paused, pause each of them
     */
  }

  playAllAudioInScene() {
    /* 
       iterate over this.audioSources array, check if not playing, play each of them
      */
  }

  muteAllAudio() {
    /* 
       iterate over this.audioSources array, save current volume info, set volume to 0
      */
  }

  unmuteAllAudio() {
    /* 
       iterate over this.audioSources array, restore previous volume info,
      */
  }

  playSingleAudio(audio) {
    audio.sound.play();
  }

  pauseSingleAudio(audio) {
    audio.sound.pause();
  }

  muteAudio(audio) {
    /* 
      set the volumeLevel of the audio to 0
       */
  }

  unMuteAudio(audio) {
    /* 
      set the volumeLevel of the audio to previous level
       */
  }
}
