import { PositionalAudio } from "three";

export default class AudioSource {
  constructor(opts) {
    this.context = opts.context;
    this.audioName = opts.name;
    this.parentMesh = opts.parentMesh;
    this.audioFileUrl = opts.audioFileUrl;
    this.volumeInfo = {
      previousVolume: 0.5,
      currentVolume: 1,
    };
    this.sound = new PositionalAudio(this.context.listener);

    this.setVolumeLevel(this.volumeInfo.currentVolume);
  }

  setVolumeLevel(volumeLevel) {
    this.sound.setVolume(volumeLevel);
  }
}
