import {
  PlaneBufferGeometry,
  Vector3,
  Mesh,
  Color,
  MeshLambertMaterial,
  DoubleSide,
  DirectionalLight,
  DirectionalLightHelper,
  Euler,
  Object3D,
} from "three";

import * as CANNON from "cannon-es";

/* 
this module contains a class definition of our custom audio object 
*/
import AudioSource from "./audio_management/audio_source";

/* 
these modules are more of a utility we use to load models and texxtures smoothly
we'll be adding more and more utilities as we go on
*/
import TexLoader from "./loaders/textureLoader";
import GLTFmodelLoader from "./loaders/GLTFmodelLoader";

export default class InitShowRoom {
  constructor(context) {
    this.context = context;

    this.settings = {
      logoDirectionalLight: {
        logoDirectionalLightColor: new Color("#fff1e0"), //halogen light
        logoDirectionalLightIntensity: 0.25,
        logoDirectionalLightPosition: new Vector3(0, 2.5, 0),
        logoDirectionalLightRotation: new Euler(90 * (Math.PI / 180), 0, 0),
        showLogoDirectionalLightHelper: false,
      },
    };

    this.gltfModelLoader = new GLTFmodelLoader(this.context);
    this.textureLoader = new TexLoader(this.context);

    this.init();

    this.addClassSettings();
  }

  async loadTextures() {}

  async loadEnvMap() {
    /* 
    todo : please replace the envmap loader with hdri loader or something like that
    */
  }

  async init() {
    this.loadEnvMap();
    this.loadTextures();

    this.testVideoTexture();

    /* 
    setting up our environment
    */
    this.cannonifyEnvt();

    this.context.envt = await this.loadEnvt();
    this.context.scene.add(this.context.envt);

    this.logoDirectionalLight = new DirectionalLight(
      this.settings.logoDirectionalLight.logoDirectionalLightColor,
      this.settings.logoDirectionalLight.logoDirectionalLightIntensity
    );

    this.logoDirectionalLight.position.copy(
      this.settings.logoDirectionalLight.logoDirectionalLightPosition
    );

    /* 

    this.logoDirectionalLight.rotation.copy(
      this.settings.logoDirectionalLight.logoDirectionalLightRotation
    ); 
    
    let lightTarget = new Object3D();
    
    lightTarget.position.set(1, 2, 3);
    this.logoDirectionalLight.target = lightTarget;
    
    console.log(this.logoDirectionalLight.rotation);

    */

    this.directionalLightHelper = new DirectionalLightHelper(
      this.logoDirectionalLight,
      0.25,
      "#0fde4f"
    );
    // this.directionalLightHelper.rotation.copy(
    //   this.logoDirectionalLight.rotation
    // );
    this.directionalLightHelper.visible =
      this.settings.logoDirectionalLight.showLogoDirectionalLightHelper;
    this.context.scene.add(this.logoDirectionalLight);
    this.context.scene.add(this.directionalLightHelper);

    this.setupSceneAudios();
  }

  setupSceneAudios() {
    /* 
    adding audio to our logo mesh
    */
    this.logoAudio = new AudioSource({
      name: "Logo Audio",
      parentMesh: this.context.envt.getObjectByName("audio_spawn_point"),
      audioFileUrl: this.context.G.AUDIO_FILE_URLS.logoAudioFile,
      context: this.context,
    });
    setTimeout(() => {
      this.context.audioManager.addAudioToScene(this.logoAudio);
    }, 5000);

    this.context.audioManager.pauseSingleAudio(this.logoAudio);
  }

  /* 
  loading the models normally, might traverse over child meshes to apply some
  material or to change some props
   */
  async loadEnvt() {
    let { model } = await this.gltfModelLoader.loadModel(
      "/new_envt/v8/progressV008.gltf"
    );

    return model;
  }

  async cannonifyEnvt() {
    let { model } = await this.gltfModelLoader.loadModel(
      "/new_envt/simplified/simplified.glb"
    );

    let allChildMeshes = {};
    for (let index = 0; index < model.children.length; index++) {
      allChildMeshes[model.children[index].name] = model.children[index];
    }

    let staticMeshes = [
      allChildMeshes.blue_circle_logo,
      allChildMeshes.chairs,
      allChildMeshes.couch,
      allChildMeshes.couch_1,
      allChildMeshes.cube_tables,
      allChildMeshes.cube_variations,
      allChildMeshes.dome_floor,
      allChildMeshes.dome_top,
      allChildMeshes.floor,
      allChildMeshes.glod_table_holder,
      allChildMeshes.gold_table_glass_top,
      allChildMeshes.interior_2nd_floor,
      allChildMeshes.mountain,
      allChildMeshes.rectangular_table,
      allChildMeshes.stairs,
      allChildMeshes.wall_shelves,
      allChildMeshes.pot_second_floor,
    ];

    staticMeshes.forEach((mesh) => {
      if (mesh) {
        this.context.physicsManager.cannonifyMeshGeometry(
          mesh,
          mesh.name,
          CANNON.Body.STATIC,
          this.context.physicsMaterial,
          0
        );
      }
    });
  }

  async testVideoTexture() {
    this.videoPlaneGeo = new PlaneBufferGeometry(10 * 16, 10 * 9, 128, 128);
    this.videoPlaneMat = new MeshLambertMaterial({
      color: 0xff0000,
      // map: this.vidTexture,
      side: DoubleSide,
    });

    this.videoPanel = new Mesh(this.videoPlaneGeo, this.videoPlaneMat);

    this.videoPanel.rotateX(90 * Math.PI);
    this.videoPanel.rotateY(60 * Math.PI);
    this.videoPanel.position.y = 10 * 9 * 0.5;
    this.videoPanel.position.z = -180;
    this.videoPanel.position.x = -180;

    await this.context.videoManager.addVideoToPlaneMesh(
      this.videoPanel,
      "/video/sintel.mp4",
      "video#1"
    );

    this.context.scene.add(this.videoPanel);
  }

  addClassSettings() {
    /* 
      logoDirectionalLight: {
      logoDirectionalLightColor: new Color("#f00"),
      logoDirectionalLightIntensity: 1,
      logoDirectionalLightPosition: new Vector3(0, 2.5, 0),
      showLogoDirectionalLightHelper: true,
      },
    */
    let localSettings = this.context.gui.addFolder("SHOW ROOM SETTINGS");

    let logoDirectionalLight = localSettings.addFolder("Logo Light");

    logoDirectionalLight.open();

    logoDirectionalLight
      .add(this.settings.logoDirectionalLight, "showLogoDirectionalLightHelper")
      .onChange((value) => {
        this.directionalLightHelper.visible = value;
      })
      .name("Show Helper");

    logoDirectionalLight
      .addColor(this.settings.logoDirectionalLight, "logoDirectionalLightColor")
      .onChange((value) => {
        this.logoDirectionalLight.color = value;
      })
      .name("Color");

    logoDirectionalLight
      .add(
        this.settings.logoDirectionalLight,
        "logoDirectionalLightIntensity",
        0,
        1,
        0.001
      )
      .onChange((value) => {
        this.logoDirectionalLight.intensity = value;
      })
      .name("Intensity");

    let logoDirectionalLightPosition =
      logoDirectionalLight.addFolder("Position");

    logoDirectionalLightPosition.open();

    logoDirectionalLightPosition
      .add(
        this.settings.logoDirectionalLight.logoDirectionalLightPosition,
        "x",
        -3,
        3,
        0.001
      )
      .onChange((value) => {
        this.logoDirectionalLight.position.x = value;
      })
      .name("X");

    logoDirectionalLightPosition
      .add(
        this.settings.logoDirectionalLight.logoDirectionalLightPosition,
        "y",
        -1,
        4,
        0.001
      )
      .onChange((value) => {
        this.logoDirectionalLight.position.y = value;
      })
      .name("Y");

    logoDirectionalLightPosition
      .add(
        this.settings.logoDirectionalLight.logoDirectionalLightPosition,
        "z",
        -3,
        3,
        0.001
      )
      .onChange((value) => {
        this.logoDirectionalLight.position.z = value;
      })
      .name("Z");
  }
}
