import { Vector3, Raycaster } from "three";
import { PointerLockControlsCannon } from "./utils/physics_utils/PointerControlCannon.js";
import * as CANNON from "cannon-es";
/* 
welcome, this module holds a class that handles all the logic needed for
 charachter movement and navigation
*/
export default class CharacterController {
  constructor(context) {
    this.context = context;

    /* 
    we setup some varaibale to hold the velocity of our charachter 
    and the direction its heading
    */

    /* 
    we create some additional booleans to help us modulate a smooth navigation
    
    for example we want to make sure the charachter is not moving on the right
    direction and on the left direction at the same time, it'd get stuck & we dont want
    the charachter to be jumping off of air, we make sure that by checking + setting theses
    boolean values accordingly based on the state of movement of the charachter :), 

    (I hope I'm not boring you with me nerding about all this stuff)
    */

    /* 
    these are just the html elements of the blocker/overlay and the instruction manual, 
    we want to hide and show them based some events like click on the screen and when "ESC" key
    is pressed
    */

    let blocker = document.getElementById("blocker");
    let instructions = document.getElementById("instructions");
    let marquees = document.getElementsByClassName("marquee-container");

    /* 
    creating the actual controller
    */
    this.context.controls = new PointerLockControlsCannon(
      this.context.camera,
      this.context.sphereBody,
      this.context
    );

    // console.log("THE CONTROLLER IS");
    // console.log(this.context.controls);
    this.context.scene.add(this.context.controls.getObject());

    /* 
    add a click listner to the overlay element, why the overlay elemet?
    => because we want the overlay to listen to clicks because we dont want to be
    annoyed while adjusting settings or while clicking other buttons in the future :/
    */

    blocker.addEventListener("click", () => {
      this.context.controls.lock();
    });

    /* 
    hide the ovrelay and instruction screen when our cursor is locked,
    */
    this.context.controls.addEventListener("lock", () => {
      instructions.style.display = "none";
      blocker.style.display = "none";

      for (let i = 0; i < marquees.length; i++) {
        marquees[i].style.display = "none";
      }

      this.context.controls.enabled = true;
    });

    /* 
    show the ovrelay and instruction screen when our cursor is locked,
    */
    this.context.controls.addEventListener("unlock", () => {
      blocker.style.display = "block";
      instructions.style.display = "";
      for (let i = 0; i < marquees.length; i++) {
        marquees[i].style.display = "block";
      }

      this.context.controls.enabled = true;
    });
  }

  update(time) {
    this.context.controls.update(time);
  }
}
