export default class DebugLog {
  constructor(context) {
    this.context = context;
  }

  log(message, showMessage = true) {
    if (this.context.debug && showMessage) {
      console.log(message);
    }
  }

  logInfo(message, showMessage = true) {
    if (this.context.debug && showMessage) {
      console.info(message);
    }
  }

  clear() {
    console.clear();
  }

  error(message, showMessage = true) {
    if (this.context.debug && showMessage) {
      console.error(message);
    }
  }

  warn(message, showMessage = true) {
    if (this.context.debug && showMessage) {
      console.warn(message);
    }
  }

  table(object, showMessage = true) {
    if (this.context.debug && showMessage) {
      console.table(object);
    }
  }
}
