import { LoadingManager } from "three";
import { gsap } from "gsap";

export default class GlobalLoaderManager {
  constructor(context) {
    this.writeOnConsole = false;

    this.context = context;

    this.context.manager = new LoadingManager();

    this.loaderContainer = document.querySelector(".loader-container");
    this.instructionsContainer = document.querySelector(".instructions");
    this.loadBarElement = document.querySelector(".loading-bar");
    this.percentSpan = document.querySelector(".percentage");
    this.percentParent = document.querySelector(".loading-percent");

    this.context.manager.onStart = (url, itemsLoaded, itemsTotal) => {
      this.debugLog(
        "Loading" +
          url +
          "\nLoaded " +
          itemsLoaded +
          " of " +
          itemsTotal +
          " files "
      );
    };

    this.context.manager.onLoad = () => {
      this.debugLog("Loading Complete");

      gsap.delayedCall(0.5, () => {
        // gsap.to(overlayMaterial.uniforms.uAlpha, { duration: 3, value: 0 });
        this.loadBarElement.classList.add("ended");
        this.percentParent.classList.add("ended");
        this.loaderContainer.classList.add("ended");
        this.loadBarElement.style.transform = "";
      });
    };

    this.context.manager.onProgress = (url, itemsLoaded, itemsTotal) => {
      this.debugLog(
        "Loading: " +
          url +
          ".\nLoaded " +
          itemsLoaded +
          " of " +
          itemsTotal +
          " files "
      );

      this.progressRatio = itemsLoaded / itemsTotal;
      this.percentSpan.innerHTML = Math.round(this.progressRatio * 100);

      this.debugLog(`loaded ${this.progressRatio * 100}% of the things =)`);
      this.loadBarElement.style.transform = `scaleX(${this.progressRatio})`;
      this.loaderContainer.style.display = `display:none`;
      // this.instructionsContainer.style.display = `flex`;
    };

    this.context.manager.onError = (url) => {
      this.debugLog("There was an error loading " + url);
    };
  }

  debugLog(message) {
    if (this.writeOnConsole) {
      console.log(message);
    }
  }
}
