import { TextureLoader, Math } from "three";

/* 
this is a module that handles texture loading, it basically do the normal
threejs texture loading with additional checks for optimization things
*/

export default class TexLoader {
  constructor(context) {
    this.context = context;

    this.tl = new TextureLoader(this.context.manager);
  }

  loadTexture(url) {
    return new Promise((resolve) => {
      this.tl.load(url, (data) => {
        /* 
        this basiaclly do a check to make sure the dimensions of the image are 
        a multiple of two, we do this checks to make sure we have an optimized
        textures in our scene, this is called the "Power of Two" rule. 

        if you are curious about it please read here =) :
         https://www.katsbits.com/tutorials/textures/make-better-textures-correct-size-and-power-of-two.php
        */
        if (
          !Math.isPowerOfTwo(data.image.width) ||
          !Math.isPowerOfTwo(data.image.height)
        ) {
          console.warn(`>>> "${url}" image size is not power of 2 <<<`);
        }

        /* 
      we need to set the needsUpdate parameter to true because we want to recompile the material
      the material that use this texture if the encoding of this texture changes after it has already
      been used by the material

      go here if you want to dig more : 
      https://threejs.org/docs/?q=texture#api/en/constants/Textures
      */
        data.needsUpdate = true;
        resolve(data);
      });
    });
  }
}
