export default class GlobalVarsInit {
  /* 
  hey there, welcome :)

  this class is all about creating a central state management of some values
  that will be needed across both the html and the threejs side of things,
  
  we'll be adding lots and lots of variables as we go along and develop more and more interactions
  that need to be managed both on the html and on the threejs systems
  */
  constructor() {
    /* 
    the G object will hold all the global params that both our systems need, for example
    the mute state of our application is needed by the html side to change icons, and the three
    js side needs to know whether an audio is muted or not to play/pause an audio properly

    so this global variable will modulate that process, one clear and simple usecase could be,

    a) user presses M on keyboard and the mute icon changes on the html side, the audio is
     paused on the threejs side, 

    b) user interacts with an audio source inside the threejs experiance and audio starts playing,
     then the mute button is toggled back on the html side

    in both a) and b) the G.MUTE variable will be used as a central command for the state of the audio

    i hope its a bit clear now :)
    */

    this.G = {
      IS_MUTE: true,

      AUDIO_FILE_URLS: {
        logoAudioFile: "/Sound/billEvans.mp3",
      },
    };

    return this.G;
  }
}
