import * as CANNON from "cannon-es";
import { Vec3 } from "cannon-es";
import CannonUtils from "../utils/physics_utils/cannonUtils.js";

import {
  Ray,
  SphereBufferGeometry,
  MeshLambertMaterial,
  Mesh,
  Vector3,
} from "three";

export default class PhysicsManager {
  constructor(context) {
    this.context = context;

    this.settings = {
      worldAllowSleep: true,
      useSplitSolver: false,

      gravity: new Vector3(0, -9.8, 0),

      globalContactMaterial: {
        friction: 0.1,
        restitution: 0.3,
      },

      playerColliderRadius: 0.425,
      playerColliderMass: 0.25,
      playerInitialPosition: new Vec3(-2, 3, -1),
    };

    this.initWorld();
    this.initCharachterCollider();
    this.initBallShooter();

    this.addClassSettings();
  }

  initWorld() {
    this.context.world = new CANNON.World({});

    this.context.world.allowSleep = this.settings.worldAllowSleep;

    this.context.world.defaultContactMaterial.contactEquationStiffness = 1e9;

    // Stabilization time in number of timesteps
    this.context.world.defaultContactMaterial.contactEquationRelaxation = 4;

    this.solver = new CANNON.GSSolver();
    this.solver.iterations = 3;
    this.solver.tolerance = 0.1;
    if (this.settings.useSplitSolver) {
      this.context.world.solver = new CANNON.SplitSolver(this.solver);
    } else {
      this.context.world.solver = this.solver;
    }

    this.context.world.gravity = this.settings.gravity;

    this.physicsMaterial = new CANNON.Material("physics");

    this.physics_physics = new CANNON.ContactMaterial(
      this.physicsMaterial,
      this.physicsMaterial,
      {
        friction: this.settings.globalContactMaterial.friction,
        restitution: this.settings.globalContactMaterial.restitution,
      }
    );

    this.context.world.addContactMaterial(this.physics_physics);

    console.log(this.context.world);

    this.groundBody = new CANNON.Body({
      type: CANNON.Body.STATIC,
      shape: new CANNON.Plane(),
    });
    this.groundBody.quaternion.setFromEuler(-Math.PI / 2, 0, 0);
    this.context.world.addBody(this.groundBody);
  }

  initCharachterCollider() {
    this.radius = this.settings.playerColliderRadius;
    this.sphereShape = new CANNON.Sphere(this.radius);
    this.context.sphereBody = new CANNON.Body({
      mass: this.settings.playerColliderMass,
      material: this.physicsMaterial,
    });
    this.context.sphereBody.addShape(this.sphereShape);
    this.context.sphereBody.position = this.settings.playerInitialPosition;
    this.context.sphereBody.linearDamping = 0.9;
    this.context.sphereBody.allowSleep = false;
    this.context.world.addBody(this.context.sphereBody);
  }

  initBallShooter() {
    this.context.balls = [];
    this.context.ballMeshes = [];

    this.shootVelocity = 15;
    this.ballShape = new CANNON.Sphere(0.2);
    this.ballGeometry = new SphereBufferGeometry(this.ballShape.radius, 32, 32);

    this.ballMaterial = new MeshLambertMaterial({
      color: 0xdddddd,
    });

    window.addEventListener("click", (event) => {
      if (!this.context.controls.enabled) {
        return;
      }

      return;

      const ballBody = new CANNON.Body({ mass: 1 });
      ballBody.allowSleep = true;
      ballBody.addShape(this.ballShape);
      const ballMesh = new Mesh(this.ballGeometry, this.ballMaterial);

      ballMesh.castShadow = true;
      ballMesh.receiveShadow = true;

      this.context.world.addBody(ballBody);
      this.context.scene.add(ballMesh);

      if (this.context.balls.length > 10) {
        this.context.balls.pop().removeShape();
        this.context.ballMeshes.pop().remove();
      }

      this.context.balls.push(ballBody);
      this.context.ballMeshes.push(ballMesh);

      const shootDirection = this.getShootDirection();
      ballBody.velocity.set(
        shootDirection.x * this.shootVelocity,
        shootDirection.y * this.shootVelocity,
        shootDirection.z * this.shootVelocity
      );

      // Move the ball outside the player sphere
      const x =
        this.context.sphereBody.position.x +
        shootDirection.x *
          (this.sphereShape.radius * 1.02 + this.ballShape.radius);
      const y =
        this.context.sphereBody.position.y +
        shootDirection.y *
          (this.sphereShape.radius * 1.02 + this.ballShape.radius);
      const z =
        this.context.sphereBody.position.z +
        shootDirection.z *
          (this.sphereShape.radius * 1.02 + this.ballShape.radius);
      ballBody.position.set(x, y, z);
      ballMesh.position.copy(ballBody.position);
    });
  }

  getShootDirection() {
    let vector = new Vector3(0, 0, 1);
    vector.unproject(this.context.camera);
    let ray = new Ray(
      this.context.sphereBody.position,
      vector.sub(this.context.sphereBody.position).normalize()
    );
    return ray.direction;
  }

  cannonifyMeshAutoHull(
    meshToBeCanonified,
    hullType,
    bodyType,
    bodyMaterial,
    bodyMass
  ) {
    let canonifiedMesh = threeToCannon(meshToBeCanonified, { type: hullType });

    let { shape } = canonifiedMesh;

    let cannonifiedBody = new CANNON.Body({
      mass: bodyMass,
      type: bodyType,
      shape,
      material: bodyMaterial,
    });

    cannonifiedBody.position.copy(meshToBeCanonified.position);
    cannonifiedBody.quaternion.copy(meshToBeCanonified.quaternion);

    this.context.world.addBody(cannonifiedBody);
  }

  cannonifyMeshGeometry(
    meshToBeCanonified,
    meshName,
    bodyType,
    bodyMaterial,
    bodyMass,
    scale = new Vec3(1, 1, 1)
  ) {
    if (!meshToBeCanonified) {
      console.log(`uh oh the mesh ${meshName} seems to be problematic`);
      return;
    }

    let shape = CannonUtils.CreateTrimesh(meshToBeCanonified.geometry);
    shape.scale.copy(scale);

    // shape.scale.set(0.75, 0.75, 0.75);
    let meshBody = new CANNON.Body({
      type: bodyType,
      material: bodyMaterial,
      mass: bodyMass,
    });

    meshBody.addShape(shape);
    meshBody.position.copy(meshToBeCanonified.position);
    meshBody.quaternion.copy(meshToBeCanonified.quaternion);

    this.context.world.addBody(meshBody);
  }

  cannonifyMeshWithCustomConvexHull(
    customConvexHullGeometry,
    meshToBeCanonified
  ) {
    let shape = CannonUtils.CreateTrimesh(customConvexHullGeometry.geometry);

    let customConvexHullBody = new CANNON.Body({
      material: this.physicsMaterial,
    });

    customConvexHullBody.addShape(shape);

    customConvexHullBody.position.copy(meshToBeCanonified.position);
    customConvexHullBody.quaternion.set(0, 0, 0, 1);

    console.log(customConvexHullBody);

    this.context.world.addBody(customConvexHullBody);
  }

  addClassSettings() {
    /* 
      worldAllowSleep: true,
      useSplitSolver: false,

      gravity: new Vector3(0, -9.8, 0),

      globalContactMaterial: {
        friction: 0.1,
        restitution: 0.3,
      },

      playerColliderRadius: 0.425,
      playerColliderMass: 0.25,
      playerInitialPosition: new Vector3(-2, 3, -1),
    */
    let localSettings = this.context.gui.addFolder("GLOBAL PHYSICS PROPERTIES");
    localSettings.add(this.settings, "worldAllowSleep").name("Allow Sleep");
    localSettings
      .add(this.settings.gravity, "y", -20, 5, 0.25)
      .name("Gravity Value");

    localSettings
      .add(this.settings.globalContactMaterial, "friction", 0, 1, 0.01)
      .name("Global Friction")
      .onChange((value) => {
        this.context.world.contactmaterials[0].friction = value;
      });
    localSettings
      .add(this.settings.globalContactMaterial, "restitution", 0, 1, 0.01)
      .name("Global Restitution")
      .onChange((value) => {
        this.context.world.contactmaterials[0].restitution = value;
        this.context.debugLog.error(this.context.world);
      });

    let playerColliderSettings = localSettings.addFolder(
      "PLAYER COLLIDER SETTINGS"
    );

    playerColliderSettings.open();

    playerColliderSettings
      .add(this.settings, "playerColliderRadius", 0.1, 0.5, 0.001)
      .onChange((value) => {
        this.context.sphereBody.shapes[0].radius = value;
      })
      .name("Radius");

    playerColliderSettings
      .add(this.settings, "playerColliderMass", 0.1, 1, 0.05)
      .onChange((value) => {
        this.context.sphereBody.mass = value;
      })
      .name("Mass");

    let playerInitialPosition = playerColliderSettings.addFolder(
      "PLAYER INITIAL POSITION"
    );

    playerInitialPosition.open();

    playerInitialPosition
      .add(this.settings.playerInitialPosition, "x", -5, 5, 0.5)
      .onChange((value) => {
        this.context.sphereBody.position.X = value;
      })
      .name("X");

    playerInitialPosition
      .add(this.settings.playerInitialPosition, "y", 0, 15, 0.5)
      .onChange((value) => {
        this.context.sphereBody.position.X = value;
      })
      .name("Y");

    playerInitialPosition
      .add(this.settings.playerInitialPosition, "z", -5, 5, 0.5)
      .onChange((value) => {
        this.context.sphereBody.position.X = value;
      })
      .name("Z");
  }
}
