import { VideoTexture } from "three";

export default class VideoManager {
  constructor(context) {
    this.context = context;

    this.videoContainer = document.getElementById("video-container");
  }

  async addVideoToPlaneMesh(planeMesh, videoSource, videoId) {
    /* 
      for now we will be accepting only MP4 videos =)
      */

    await this.mountVideoElement(videoSource, videoId);

    this.video = document.getElementById(videoId);
    this.video.load();
    // this.video.play();

    this.vidTexture = new VideoTexture(this.video);

    planeMesh.material.map = this.vidTexture;

    this.setupVideoControllers();
  }

  async mountVideoElement(videoSource, videoId) {
    this.videoContainer.insertAdjacentHTML(
      "beforeend",
      this.getVideoTagTemplate(videoSource, videoId)
    );
  }

  getVideoTagTemplate(videoSource, videoId) {
    return `
      <video
        loop
        id=${videoId}
        crossorigin="anonymous"
        playsinline
        style="display: none"
        allow="autoplay"
      >
        <source
          src=${videoSource}
          type='video/mp4; codecs="avc1.42E01E, mp4a.40.2"'
        />
      </video>
     `;
  }

  setupVideoControllers() {
    window.addEventListener("keypress", (e) => {
      if (e.code == "KeyV") {
        console.log("playing the video");
        this.video.play();
      } else if (e.code == "KeyZ") {
        console.log("pausing the video");
        this.video.pause();
      }
    });
  }
}
