import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import { DRACOLoader } from "three/examples/jsm/loaders/DRACOLoader";

export default class GLTFmodelLoader {
  /* 
gltf model loader module with draco loader(draco loader part can be commented
  out if the model to be processed is not compressed)

this module return the model and an array of animations of the 
given model so that these components of the model can be processed
separetly
*/
  constructor(context) {
    this.context = context;

    this.loader = new GLTFLoader(this.context.manager);

    this.dracoLoader = new DRACOLoader();
    this.dracoLoader.setDecoderPath(
      "https://www.gstatic.com/draco/v1/decoders/"
    );
    this.dracoLoader.setDecoderConfig({ type: "js" });
    this.dracoLoader.preload();

    this.loader.setDRACOLoader(this.dracoLoader);
  }

  loadModel(url) {
    return new Promise((resolve, reject) => {
      this.loader.load(
        url,
        (gltf) => {
          /* 
      please destructure the returned object like : 

      let {model} = modelLoaderGLTF("url") => to get only the model
      let {animations} = modelLoaderGLTF("url") => to get the array of animations

      you can also do => let {model , animations } too 

      please add more and more components you want to import on the result object
      and process them as stated above

      */
          let result = { model: gltf.scene, animations: gltf.animations };
          resolve(result);
        },
        (e) => {
          // console.log("LOADING THE RESOURCE");
        },
        (err) => {
          console.log(err);
        }
      );
    });
  }
}
